/* App.css */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.Main {
  background-color: #f2f4fb;
  text-align: center;

  display: block;
  margin-left: auto;
  margin-right: auto;
  min-height: 100vh;
  border: 3px;
  box-sizing: border-box;
  width: min(500px, 100%);
  box-shadow: 10px 20px 40px rgba(0, 0, 0, 0.1),
    -10px 20px 40px rgba(0, 0, 0, 0.1);

  padding-bottom: 10px;
}
